.create_challenge{
    min-height: 60vh;
    max-height: 60vh;
    overflow:auto;
    >.create_top{
        position:fixed;
        top:0.5rem;
        right:0.5rem;
        border-bottom: 1px solid gray;
        width:100%;
        height: 35px;
        display:flex;
        align-items: center;
        justify-content: center;
        >h2{
            text-align: center;
            font-size: 0.8rem;
            
        }
    }

    >.create_footer{
        position: fixed;
        bottom:0;
        background-color: indianred;
        width:100%;
        height: 50px;
        display:flex;
        align-items: center;
        >button{
            display:flex;
            flex-direction: column;
            margin:0.5rem;
            flex:1;
            align-items: center;
            border:none;
            background-color: transparent;
            color:white;
            font-size: 0.7rem;
        }

        >button.active{
            border-bottom: 1px solid white;
            font-weight: bold;
        }
    }

    >.create_close_btn{
        position: fixed;
        top:0;
        right:0;
        width:30px;
        height: 30px;
        border-radius: 50%;
        border:none;
        background-color: indianred;
        color:"white";
        display:flex;
        align-items: center;
        justify-content: center;
        z-index: 999;
    }

    >.create_submit_btn{
        position: fixed;
        bottom:110px;
        right:1rem;
        width:40px;
        height: 40px;
        border-radius: 50%;
        border:none;
        background-color: green;
        color:"white";
        display:flex;
        align-items: center;
        justify-content: center;
        z-index: 999;
    }

    >.create_content{
        margin-top: 35px;
    }
}