.login2{
    background-color: black;
    min-height: 100vh;
    color:white;
   

    >.login_body{
        flex:1;
        width:100vw;
       
        >.top{
            display:flex;
            flex-direction:column;
            align-items:center;
            padding: 1rem;
            margin-bottom:1rem;
            font-size: 0.8rem;
           
            >p{
                font-weight: bold;
                text-align:center;
            }

            >.brand{
                font-size: 1rem;
               
            }
        }

        >.form{

            display:flex;
            flex-direction:column;
            align-items:center;
            
            
            >.line{
               
                display:flex;
                width:70%;
                flex-direction:column;
                margin-bottom:1rem;
                >label{
                    font-size: 0.7rem;
                    font-weight:bold;
                    color:var(--sub_black)
                }

                >p{
                    text-align: center;
                    color:indianred;
                }

                >div{
                    
                    display:flex;
                    align-items:center;
                    background-color:var(--sub_black);
                   
                    border-radius: 5px;
                    >input{
                        flex:1;
                        border:none;
                        outline:none;
                        padding:0.7rem;
                        border-radius: 5px;
                        background-color:var(--sub_black);
                        color:silver;

                    }
                }

                >button{
                    padding:0.7rem;
                    border:none;
                    background-color:black;
                    color:white;
                    border:1px solid silver;
                    font-weight: bold;
                    font-size:0.8rem;
                    width:150px;
                    margin:auto;
                    border-radius:5px;
                    
                }

                >#btn_login{
                    display:flex;
                    align-items: center;
                    justify-content:center;
                    gap:0.5rem;
                }

                >.btn_forget{
                    background-color:transparent !important;
                    width:100%;
                    font-weight: normal;
                    color:white;
                    border:none;
                    >span{
                        color:indianred; 
                    }
                }

                >p{
                   font-size: 0.8rem;
                   >span{
                       color:indianred;
                   }
                }
            }
        }

        >.info_zone{
            padding:1rem;
            font-size: 0.7rem;
            >.or_zone{
               
                display:flex;
                position:relative;
                >div{
                    flex:1;
                    border-bottom: 1px solid silver;
                }

                >div:nth-child(2){
                    width:40px;
                    background-color:black;
                    font-size: 0.7rem;
                    border:none;
                   position:absolute;
                   right:45%;
                   bottom:-8px;
                   display:flex;
                   align-items:center;
                   justify-content:center;
                }
            }
            >.provider_zone{
                margin-top: 1rem;
                display:flex;
                align-items:center;
                justify-content:center;
                gap:0.5rem;
                >button{
                    display:flex;
                    align-items:center;
                    justify-content:center;
                    font-size: 0.7rem;
                    font-weight:bold;
                    gap:0.5rem;
                    padding:0.5rem;
                    border:none;
                    background-color:white;
                    border:1px solid silver;
                    border-radius: 3px;
                    color:black;
                    padding:8px 16px;
                    margin-top: 0.5rem;
                    box-shadow: 0 2px 2px 0 rgba(0,0,0,.14),0 3px 1px -2px rgba(0,0,0,.2),0 1px 5px 0 rgba(0,0,0,.12);
                }
                
            }
            
        }

        
    }

    >.login_footer{
        position:fixed;
        bottom:0;
        height: 60px;
        background-color:var(--sub_black);
        width:100%;
        display:flex;
        align-items:center;
        justify-content:center;
        text-align: center;
        

        >p{
            font-size: 0.8rem;
            text-align: center;
            display:flex;
            gap:0.5rem;
            >span{
                font-weight: bold;
            }
        }
    }
}